import TYPES from '../jssdk/types';
import { inject, injectable } from 'inversify';

@injectable()
export class AppConfig implements AppConfig {

    //  | 
    @inject(TYPES.Utils) private _: Utils;

    private container: Obj = {
        /**
         * 
         * 
         */
        reportType1: [
            'batchId', /*            */
            'appId', /*             Id */
            'appName', /*            */
            'sysId', /*             Id */
            'sysName', /*            */
            'origin', /*             */
            'openId', /*            openId */
            'clientType', /*         */
            'sysVersion', /*         */
            'ip', /*                IP */
            'userNetWork', /*        */
            'createTime' /*          */
        ],
        reportType2: [
            'batchId', /*            */
            'sysId', /*             Id */
            'pageId', /*            Path */
            'pageName', /*           */
            'pageUrl', /*            */
            'funcId', /*            Id */
            'funcName', /*           */
            'funcIntention', /*      */
            'preFuncId', /*         Id */
            'eventId', /*           Id eventTypeid */
            'eventName', /*          */
            'eventType', /*          */
            'eventTime', /*          */
            'createTime', /*         */
            'pageDwellTime', /*      */
            'enterTime', /*          */
            'leaveTime' /*           */
        ],
        //  iframe 
        // insertIframeSrc: 'https://sales-dev.ihxlife.com/video/hx-analytics/jssdk/index.html'
    };

    set(key: string, data: Obj) {
        this.container[key] = data;
    }
    get(key: string = '') {
        return key ?
                this.container[key] :
                JSON.parse(JSON.stringify(this.container));
    }
    merge(data: Obj) {
        this.container = { ...this.container, ...data };
    }
    // getSelf() {
    //     return JSON.parse(JSON.stringify(this.container));
    // }
}